/* eslint-disable react-hooks/exhaustive-deps */
import { createElement, HTMLAttributes, useEffect, useRef } from "react";

import Quill, { Delta } from "quill/core";

import Snow from "quill/themes/snow";
import Toolbar from "quill/modules/toolbar";

import Bold from "quill/formats/bold";
import Italic from "quill/formats/italic";
import Underline from "quill/formats/underline";
import Link from "quill/formats/link";
import Strike from "quill/formats/strike";
import { AlignStyle as Align } from "quill/formats/align";

Quill.register("modules/toolbar", Toolbar);

Quill.register("themes/snow", Snow);
Quill.register("formats/bold", Bold);
Quill.register("formats/italic", Italic);
Quill.register("formats/underline", Underline);
Quill.register("formats/link", Link);
Quill.register("formats/strike", Strike);
Quill.register("formats/align", Align);

import getToolbar from "./toolbar";

interface RichTextProps {
  tagName?: keyof JSX.IntrinsicElements;
  onChange?: (value: string) => void;
  value?: string | Delta;
  preventNewLine?: boolean;
  toolbar?: any;
  props?: HTMLAttributes<HTMLDivElement>;
  placeholder?: string;
  onAlignmentChange?: (alignment: string) => void;
  onSplit?: (beforeCursorHtml: Delta, afterCursorHtml: Delta) => void;
}

const defaultToolbar = [
  ["bold", "italic", "underline", "strike"],
  ["link"],
  ["align"],
];

const RichText = ({
  tagName: Wrapper = "div",
  onChange,
  preventNewLine,
  value = "",
  toolbar = defaultToolbar,
  props,
  placeholder,
  onAlignmentChange,
  onSplit,
}: RichTextProps) => {
  const elRef: any = useRef<HTMLElement>();
  const toolbarRef: any = useRef<HTMLElement>();
  const quillRef = useRef<Quill>();
  const changedHere = useRef(true);
  const changedProps = useRef(false);
  const isFocused = useRef(false);

  useEffect(() => {
    if (!quillRef.current) {
      return;
    }
    if (changedHere.current) {
      changedHere.current = false;
      return;
    }
    changedProps.current = true;

    if (typeof value === "string") {
      quillRef.current.root.innerHTML = value;
    } else {
      quillRef.current.setContents(value);
    }
  }, [value, quillRef.current]);

  useEffect(() => {
    const editor = elRef.current as HTMLElement;
    if (!editor || editor.dataset.wptbHasQuill) {
      if (isFocused.current) {
        document.body.appendChild(toolbarRef.current);
      }
      return () => toolbarRef.current?.remove();
    }

    toolbarRef.current = getToolbar(toolbar);

    if (typeof value === "string") {
      editor.innerHTML = value;
    }
    editor.dataset.wptbHasQuill = "true";
    quillRef.current = new Quill(editor as HTMLElement, {
      theme: "snow",
      modules: {
        toolbar: toolbarRef.current,
      },
      placeholder: placeholder ?? "Content here...",
    });

    quillRef.current.root.setAttribute("spellcheck", "false");

    if (typeof value !== "string") {
      quillRef.current.setContents(value);
      onChange?.(quillRef.current.root.innerHTML);
    }
    changedProps.current = false;

    quillRef.current.on("selection-change", (range, oldRange) => {
      if (!oldRange) {
        document.body.appendChild(toolbarRef.current);
        isFocused.current = true;

        const box = elRef.current.getBoundingClientRect();
        const tBox = toolbarRef.current.getBoundingClientRect();
        toolbarRef.current.style.top = `${box.top}px`;
        toolbarRef.current.style.left = `${box.left - (tBox.width - box.width) / 2}px`;

        const currentAlign = quillRef.current.getFormat().align || "";
        const alignEl = toolbarRef.current.querySelector(
          `.ql-align[value="${currentAlign}"]`
        );
        if (alignEl) {
          toolbarRef.current
            .querySelector(".ql-aligns .ql-wptb-active")
            ?.classList.remove("ql-wptb-active");
          alignEl.classList.add("ql-wptb-active");
          toolbarRef.current.querySelector(
            ".ql-aligns .ql-picker-label"
          ).innerHTML = alignEl.innerHTML;
        }
      } else if (!range) {
        toolbarRef.current.remove();
        isFocused.current = false;
      }
    });

    return () => toolbarRef.current?.remove();
  }, [elRef.current]);

  useEffect(() => {
    if (!quillRef.current) {
      return;
    }

    const changeEvt = (_delta: any, _oldDelta: any, source: any) => {
      if (source === "user") {
        if (changedProps.current) {
          changedProps.current = false;
          return;
        }
        changedHere.current = true;

        onChange?.(quillRef.current.root.innerHTML);
      }
    };

    quillRef.current.on("text-change", changeEvt);

    return () => {
      quillRef.current?.off("text-change", changeEvt);
    };
  }, [quillRef.current, onChange]);

  useEffect(() => {
    if (!quillRef.current) {
      return;
    }

    const alignModule: any = quillRef.current.getModule("toolbar");
    if (alignModule) {
      alignModule.handlers["align"] = (value: string) => {
        if (onAlignmentChange) {
          onAlignmentChange?.(value);
        } else {
          quillRef.current.format("align", value);
        }
      };
    }
  }, [quillRef.current, onAlignmentChange]);

  useEffect(() => {
    if (!quillRef.current) {
      return;
    }

    const keyboard = quillRef.current.getModule("keyboard") as any;
    const enterBind = keyboard.bindings["Enter"];

    keyboard.bindings["Enter"] = [
      {
        key: 13,
        handler: () => {
          const quill = quillRef.current;

          if (onSplit) {
            const selection = quill.getSelection();
            if (selection) {
              const cursorPosition = selection.index;

              const delta = quill.getContents();

              const beforeCursorDelta = delta.slice(0, cursorPosition);
              const afterCursorDelta = delta.slice(cursorPosition);

              onSplit(beforeCursorDelta, afterCursorDelta);
            }
            return false;
          }

          if (preventNewLine) {
            return false;
          }
          return true;
        },
      },
    ];

    return () => {
      keyboard.bindings["Enter"] = enterBind;
    };
  }, [preventNewLine, quillRef.current, onSplit]);

  return createElement(Wrapper, { ...props, ref: elRef });
};
export default RichText;
