import { EnhancementGroup } from "@/builder/components/enhanced-control";
import UpsellControl from "@/builder/components/upsell-control";
import { RootState } from "@/store";
import { TABLE_ID } from "@/store/table";
import TimeTravel from "@/store/time-travel";
import PanelBody from "@wptb/ui/components/panel-body";
import SizeControl from "@wptb/ui/components/size-control";
import SwitchControl from "@wptb/ui/components/switch-control";
import { ColumnProps, TableProps } from "@wptb/dev/types";
import { useSelector } from "react-redux";

interface CtrlProps {
  id: string;
  index: number;
}

const ColControl = ({ id, index }: CtrlProps) => {
  const tableProps: TableProps = useSelector(
    (state: RootState) => state.table[id].blocksProps[TABLE_ID]
  );
  const blockProps = tableProps.columns?.[index] || {};
  const setBlockProps = (props: Partial<ColumnProps>) => {
    TimeTravel.updateProps(TABLE_ID, {
      columns: {
        ...tableProps.columns,
        [index]: {
          ...blockProps,
          ...props,
        },
      },
    });
  };
  return (
    <PanelBody title="Column Control" className="wptb-settings">
      <EnhancementGroup
        name="column"
        props={blockProps}
        setProps={setBlockProps}
      />
      <SizeControl
        label="Column Width"
        value={blockProps.width}
        onChange={(value) => setBlockProps({ width: value })}
        allowedUnits={{
          px: {
            max: 500,
          },
        }}
        allowOutOfRange
      />
      <UpsellControl name="global.highlight_col" noOverlayText>
        <SwitchControl
          label="Highlight this column"
          checked={blockProps.highlight as any}
          onChange={(value) => setBlockProps({ highlight: value })}
        />
      </UpsellControl>
      <SwitchControl
        label="Hide this column on mobile"
        checked={blockProps.hideOnMobile as any}
        onChange={(value) => setBlockProps({ hideOnMobile: value })}
      />
    </PanelBody>
  );
};

export default ColControl;
