import { v4 as uuidv4 } from "uuid";
import {
  ResponsiveDeviceName,
  TablesState,
  TableState,
  TableStateDevice,
} from "@wptb/dev/types";
import { CellProps, ResponsiveDeviceProps, TableProps } from "@wptb/dev/types";
import { TABLE_ID } from ".";
import { PayloadAction } from "@reduxjs/toolkit";

const toColStack = (
  { innerBlocks, blockOrder }: TableStateDevice,
  { itemsPerHead }: ResponsiveDeviceProps
): TableStateDevice => {
  const newBlockOrder: string[] = [];
  for (let i = 1; i < blockOrder.length; i++) {
    if ((i - 1) % itemsPerHead === 0) {
      newBlockOrder.push(blockOrder[0]);
    }
    newBlockOrder.push(blockOrder[i]);
  }
  return {
    innerBlocks,
    blockOrder: newBlockOrder,
  };
};

const toRowStack = (
  table: TableState,
  { itemsPerHead, topRowAsHeader }: ResponsiveDeviceProps
): TableStateDevice => {
  const blocksProps = table.blocksProps;
  const newBlockOrder: string[] = [];
  const newInnerBlocks: Record<string, string[]> = {};
  const header: Record<
    number,
    {
      id: string;
      rowspan: number;
      colspan: number;
    }
  > = {};
  const { blockOrder: fOrder, innerBlocks: fiBlocks } = table.desktop;

  let i = 0;
  if (topRowAsHeader) {
    itemsPerHead += 1;
    i++;
    const cells = fiBlocks[fOrder[0]];
    for (let i = 0; i < cells.length; i++) {
      const id = cells[i];
      const props = blocksProps[id];
      header[props.col] = {
        id: id,
        rowspan: Math.min(props.rowspan, itemsPerHead - 1),
        colspan: props.colspan,
      };
    }
  }

  const colMap: Record<
    number,
    {
      row: string[];
      length: number;
    }
  > = {};

  const fillRowColSpans = (col: number, colspan: number, rowspan: number) => {
    for (let k = 0; k < colspan; k++) {
      const fillerColIdx = col + k;
      let fillerRow = colMap[fillerColIdx];
      if (!fillerRow || fillerRow.length + rowspan > itemsPerHead) {
        const newRowId = uuidv4();
        fillerRow = {
          row: [],
          length: 0,
        };
        colMap[fillerColIdx] = fillerRow;
        newBlockOrder.push(newRowId);
        newInnerBlocks[newRowId] = fillerRow.row;
      }

      fillerRow.length += rowspan;
    }
  };

  for (; i < fOrder.length; i++) {
    const cells = fiBlocks[fOrder[i]];
    for (let j = 0; j < cells.length; j++) {
      const cellId = cells[j];
      const { col, colspan, rowspan }: CellProps = blocksProps[cellId];
      let targetRow = colMap[col];
      if (!targetRow || targetRow.length + rowspan > itemsPerHead) {
        const newRowId = uuidv4();
        targetRow = {
          row: [],
          length: 0,
        };
        colMap[col] = targetRow;
        newBlockOrder.push(newRowId);
        newInnerBlocks[newRowId] = targetRow.row;

        const head = header[col];
        if (head) {
          targetRow.row.push(head.id);
          fillRowColSpans(col, head.colspan, head.rowspan);
        }
      }

      targetRow.row.push(cellId);
      fillRowColSpans(col, colspan, rowspan);
    }
  }

  return {
    innerBlocks: newInnerBlocks,
    blockOrder: newBlockOrder,
  };
};

export const internalSetResponsive = (
  tables: TablesState,
  action: PayloadAction<{
    device: ResponsiveDeviceName;
    tableId: string;
  }>
) => {
  const { device, tableId } = action.payload;
  if (device === "desktop") {
    return;
  }
  const state = tables[tableId];
  let table = state.desktop;
  const mainConfig: TableProps["responsive"] =
    state.blocksProps[TABLE_ID]?.responsive;
  const config = mainConfig?.[device];

  if (!mainConfig?.enable || config?.disable) {
    state[device] = table;
    return;
  }

  if (config?.stackDirection === "row") {
    table = toRowStack(state, config);
  } else if (config?.topRowAsHeader) {
    table = toColStack(table, config);
  }
  state[device] = table;
};

export const toResponsive = (
  state: TableState,
  device: ResponsiveDeviceName
): TableStateDevice => {
  let table = state.desktop;
  if (device === "desktop") {
    return table;
  }
  const mainConfig: TableProps["responsive"] =
    state.blocksProps[TABLE_ID]?.responsive;
  const config = mainConfig?.[device];

  if (!mainConfig?.enable || config?.disable) {
    return table;
  }
  if (config?.stackDirection === "row") {
    table = toRowStack(state, config);
  } else if (config?.topRowAsHeader) {
    table = toColStack(table, config);
  }
  return table;
};
