import Cell from "./Cell";
import { RowProps, TableProps } from "@wptb/dev/types";
import { useMemo } from "react";
import { useBlockProps } from "@/public/hooks";
import { getRowAttrs } from "./get-attrs";

interface RowRCProps {
  id: string;
  tableId: string;
  tableProps: TableProps;
  rowIndex: number;
  blockOrder: string[];
}

const Row = ({ id, tableId, blockOrder, tableProps, rowIndex }: RowRCProps) => {
  const props = useBlockProps<RowProps>([tableId, id]);
  const attrs = useMemo(
    () => getRowAttrs(props, rowIndex, tableProps),
    [props, tableProps, rowIndex, blockOrder]
  );

  return (
    <tr {...attrs}>
      {blockOrder.map((cellId, cellIndex) => (
        <Cell
          key={cellId}
          id={cellId}
          tableId={tableId}
          rowId={id}
          rowIndex={rowIndex}
          cellIndex={cellIndex}
        />
      ))}
    </tr>
  );
};

export default Row;
