import UpsellControl from "@/builder/components/upsell-control";
import PanelBody from "@wptb/ui/components/panel-body";
import SizeControl from "@wptb/ui/components/size-control";
import SwitchControl from "@wptb/ui/components/switch-control";
import { BlockControlProps } from "@wptb/dev/types";
import { RowProps } from "@wptb/dev/types";

const RowControl = ({
  blockProps,
  setBlockProps,
}: BlockControlProps<RowProps>) => {
  return (
    <PanelBody title="Row Control" className="wptb-settings">
      <SizeControl
        label="Row Height"
        value={blockProps.height}
        onChange={(value) => setBlockProps({ height: value })}
        allowOutOfRange
        allowedUnits={{
          px: {
            max: 500,
          },
        }}
      />
      <UpsellControl name="global.highlight_row" noOverlayText>
        <SwitchControl
          label="Highlight this row"
          checked={blockProps.highlight as any}
        />
      </UpsellControl>
    </PanelBody>
  );
};

export default RowControl;
