import { CellProps, RowProps, TableProps } from "@wptb/dev/types";
import { Border, BorderSide } from "@wptb/dev/types";
import {
  getBorderStyle,
  getSingleBorderStr,
  getSpacingStyle,
} from "@wptb/utils/style";
import cn from "classnames";
import { CSSProperties } from "react";

export const calcTableMinWidth = (props: TableProps) => {
  let minWidth = "100%",
    maxWidth;
  const cellMinWidth = parseInt(props.cellMinWidth || "120px");
  let total = props.cols * cellMinWidth;
  let cellPadding;
  if (props.cellPadding) {
    if (typeof props.cellPadding === "string") {
      cellPadding = parseInt(props.cellPadding);
      total += cellPadding * 2 * props.cols;
    }
  }

  if (props.innerBorder) {
    if ((props.innerBorder as BorderSide).width) {
      total +=
        parseInt((props.innerBorder as BorderSide).width) * (props.cols + 1);
    } else {
      total += parseInt((props.innerBorder as Border).left.width) * props.cols;
      total += parseInt((props.innerBorder as Border).right.width) * props.cols;
    }
  }
  if (props.tableBorder) {
    if ((props.tableBorder as BorderSide).width) {
      total += parseInt((props.tableBorder as BorderSide).width) * 2;
    } else {
      total += parseInt((props.tableBorder as Border).left.width);
      total += parseInt((props.tableBorder as Border).right.width);
    }
  }
  if (props.maxWidth) {
    maxWidth = parseInt(props.maxWidth);
  }
  if (!maxWidth || total > maxWidth) {
    minWidth = `${total}px`;
  }

  return { minWidth, maxWidth, total, cellMinWidth };
};

export const getTableStyle = (props: TableProps, minWidth: any): any => {
  return {
    minWidth,
    ...getBorderStyle(props.tableBorder),
    borderCollapse: props.separateRowCol ? "separate" : "collapse",
    "--wptb-border-spacing": `${props.colSpacing || 0} ${props.rowSpacing || 0}`,
    "--wptb-link-color": props.linkColor,
    "--wptb-inner-border": getSingleBorderStr(props.innerBorder as BorderSide),
    "--wptb-inner-radius": props.innerBorderRadius,
  } as CSSProperties;
};

export const getTableAttrs = (
  props: TableProps,
  calced: ReturnType<typeof calcTableMinWidth>
) => {
  return {
    className: cn("wptb-preview-table wptb-element-main-table_setting-404", {
      "wptb-row-borders-only": props.rowBorderOnly,
      "wptb-column-borders-only": props.columnBorderOnly,
      "wptb-no-header-border": !props.headerInnerBorder,
    }),

    "data-wptb-horizontal-scroll-status": props["scrollX"],
    "data-wptb-first-column-sticky": props["stickyFirstColumn"],
    "data-wptb-pro-pagination-top-row-header":
      props["paginationTopRowAsHeader"],
    "data-wptb-rows-per-page": props["rowsPerPage"],
    "data-wptb-pro-search-top-row-header": props["searchKeepHeader"],
    "data-wptb-searchbar-position": props["searchPosition"],
    role: props["role"],
    "data-table-columns": props["cols"],
    "data-wptb-table-alignment": props["alignment"],
    "data-wptb-td-width-auto": calced.cellMinWidth,
    "data-wptb-table-tds-sum-max-width": calced.total,
    "data-disable-theme-styles": props["disableThemeStyles"] ? 1 : undefined,
    "data-wptb-search-enable": props["searchEnable"],

    "data-wptb-header-background-color": props["headerBg"],
    "data-wptb-even-row-background-color": props["evenRowBg"],
    "data-wptb-odd-row-background-color": props["oddRowBg"],
    "data-wptb-header-hover-background-color": props["hoverHeaderBg"],
    "data-wptb-even-row-hover-background-color": props["hoverEvenRowBg"],
    "data-wptb-odd-row-hover-background-color": props["hoverOddRowBg"],
    "data-wptb-sortable-table-horizontal": props.sortHorizontal ? 1 : undefined,
    "data-wptb-sortable-table-vertical": props.sortVertical ? 1 : undefined,
  };
};

export const getTBodyAttrs = (props: TableProps) => {
  return {
    "data-global-font-color": props["fontColor"],
    "data-global-link-color": props["linkColor"],
    "data-global-font-size": props.fontSize
      ? parseInt(props.fontSize)
      : undefined,
  };
};

/* ============ ROW ============= */

export const getRowAttrs = (
  props: RowProps,
  rowIndex: number,
  tableProps: TableProps
) => {
  let className = "wptb-row";
  const attrs: Record<string, any> = {};
  if (tableProps.stickyTopRow && rowIndex === 0) {
    attrs["data-wptb-sticky-top-row"] = "true";
  }

  let hoverColor: string | undefined;
  let background;

  if (rowIndex % 2 === 0) {
    hoverColor = tableProps.hoverOddRowBg;
    background = tableProps.oddRowBg;
  } else {
    hoverColor = tableProps.hoverEvenRowBg;
    background = tableProps.evenRowBg;
  }

  if (rowIndex === 0) {
    hoverColor = tableProps.hoverHeaderBg || hoverColor;
    background = tableProps.headerBg || background;
  }

  if (hoverColor) {
    className += " wptb-row-has-hover";
  }
  if (!background) {
    background = props?.background;
  }

  if (
    tableProps.innerBorderRadius &&
    !(tableProps.rowBorderOnly || tableProps.columnBorderOnly)
  ) {
    background = "white";
  }

  attrs.className = className;
  attrs.style = {
    background,
    "--hover-bg-color": hoverColor,
    height: props?.height,
  };
  if (props?.highlight) {
    attrs.style.transform = `scale(${(props.highlightScale || 100) / 100})`;
  }
  return attrs;
};

/* ============ CELL ============= */

export const getCellAttrs = (
  props: CellProps,
  rowStack: boolean,
  tableProps: TableProps
) => {
  const sortAttr: Record<string, any> = {};
  if (tableProps.sortVertical?.enable) {
    if (props.row === 0) {
      sortAttr["data-sorted-vertical"] =
        tableProps.sortVertical.order === "desc" ? "desk" : "ask";
    }
  } else if (tableProps.sortHorizontal?.enable && props.col === 0) {
    sortAttr["data-sorted-horizontal"] =
      tableProps.sortHorizontal.order === "desc" ? "desk" : "ask";
  }

  return {
    ...(!rowStack
      ? { colSpan: props.colspan, rowSpan: props.rowspan }
      : {
          colSpan: props.rowspan,
          rowSpan: props.colspan,
        }),
    "data-row": props.row,
    "data-col": props.col,
    ...sortAttr,
  };
};

export const getCellStyle = (props: CellProps, tableProps: TableProps) => {
  const column = tableProps.columns?.[props.col];
  const style: CSSProperties = {
    ...getSpacingStyle(tableProps.cellPadding, "padding"),
    ...getBorderStyle(props.border),
    backgroundColor: props.background,
    verticalAlign: props.vAlign,
    width: column?.width,
    color: tableProps.fontColor,
    fontSize: tableProps.fontSize,
  };
  if (column?.highlight) {
    style.transform = `scale(${(column.highlightScale || 100) / 100})`;
  }
  return style;
};
