import { useMemo } from "react";
import { CellProps, TableProps } from "@wptb/dev/types";
import { useBlockProps } from "@/public/hooks";
import FloatingActions from "../actions/floating";
import { useDispatch, useSelector } from "react-redux";
import { RootState } from "@/store";
import { CellMerging } from "../actions/panel/merging";
import cls from "classnames";
import { setActiveBlock } from "@/store/editor";
import { TABLE_ID } from "@/store/table";
import Block from "./Block";
import RowColActions from "../actions/floating/row-col-actions";
import { getCellAttrs, getCellStyle } from "./get-attrs";

interface CellRCProps {
  id: string;
  tableId: string;
  rowId: string;
  rowIndex: number;
  cellIndex: number;
}

const Cell = ({ id, rowId, rowIndex, cellIndex, tableId }: CellRCProps) => {
  const dispatch = useDispatch();
  const isSelected = useSelector(
    (state: RootState) => state.editorSettings.selectedCells?.[id]
  );
  const isActive = useSelector(
    (state: RootState) => state.editorSettings.activeBlock?.at(-1) === id
  );
  const hasActive = useSelector(
    (state: RootState) => state.editorSettings.activeBlock?.[2] === id
  );
  const isRowStack = useSelector((state: RootState) => {
    const device = state.editorSettings.devicePreview;
    if (device === "desktop") {
      return false;
    }
    const opts = (state.table[tableId].blocksProps[TABLE_ID] as TableProps)
      ?.responsive;
    if (!opts?.enable) {
      return false;
    }

    const deviceCfg = opts?.[device];
    if (!deviceCfg || deviceCfg.disable) {
      return false;
    }

    return deviceCfg.stackDirection === "row";
  });
  const tableProps = useBlockProps<TableProps>([tableId, TABLE_ID]);
  const props = useBlockProps<CellProps>([tableId, id]);
  const { attrs } = useMemo(() => {
    const attrs: Record<string, any> = getCellAttrs(
      props,
      isRowStack,
      tableProps
    );
    attrs.style = getCellStyle(props, tableProps);
    return { attrs };
  }, [props, tableProps, rowIndex, isRowStack]);

  const blockOrder = useSelector(
    (state: RootState) => state.table[tableId].innerBlocks[id].blockOrder
  );
  const isEmpty = props.isEmpty;

  const innerBlocks = useMemo(() => {
    const ancestor = [tableId, rowId, id];
    return isEmpty
      ? null
      : blockOrder.map((blockId, idx) => (
          <Block key={blockId} id={blockId} index={idx} ancestor={ancestor} />
        ));
  }, [rowId, id, isEmpty, blockOrder]);

  return (
    <td
      {...attrs}
      className={cls({
        "wptb-selected-cell": isSelected,
        "wptb-empty": props.isEmpty,
        "wptb-cell-placeholder": !blockOrder.length,
        "wptb-cell-active": isActive,
        "wptb-cell-has-active": hasActive,
        "wptb-row-stack-cell": isRowStack,
        "wptb-row-ending-cell": props.col + props.colspan === tableProps.cols,
      })}
      data-x-index={cellIndex}
      data-y-index={rowIndex}
      onMouseLeave={FloatingActions.onCellLeave}
      onMouseEnter={(evt) => FloatingActions.onCellEnter(evt, id)}
      onMouseMove={FloatingActions.onCellMove}
      onPointerDownCapture={(evt) => CellMerging.markCells(evt, id)}
      onClick={(evt: any) => {
        if (!evt.wptbActivatedEdit) {
          dispatch(setActiveBlock([tableId, rowId, id]));
        }
      }}
    >
      {innerBlocks}
      {tableProps.sortVertical?.enable || tableProps.sortHorizontal?.enable ? (
        <button
          className="wptb-sort-button"
          onClick={(evt) => {
            if (tableProps.sortVertical?.enable) {
              RowColActions.sortTableV(
                evt.currentTarget.parentElement as HTMLTableCellElement,
                props.row,
                tableProps.sortVertical
              );
            } else if (tableProps.sortHorizontal?.enable) {
              RowColActions.sortTableH(
                evt.currentTarget.parentElement as HTMLTableCellElement,
                props.col,
                tableProps.sortHorizontal
              );
            }
          }}
        />
      ) : null}
    </td>
  );
};

export default Cell;
