interface SelectControlProps {
  label?: string;
  value?: string;
  onChange?: (value: string) => void;
  options: {
    label: string;
    value: string;
  }[];
}
export default function SelectControl({
  label,
  value,
  onChange,
  options,
}: SelectControlProps) {
  return (
    <label className="wptb-select-control wptb-control">
      {label && <p className="wptb-select-control-label">{label}</p>}
      <select value={value || ""} onChange={(e) => onChange?.(e.target.value)}>
        {options.map((option) => (
          <option key={option.value} value={option.value}>
            {option.label}
          </option>
        ))}
      </select>
    </label>
  );
}
